"use client";

import { useState, useEffect, useCallback, useRef } from "react";

interface TimerProps {
  startedAt: string; // ISO —Å—Ç—Ä–æ–∫–∞ –¥–∞—Ç—ã
  duration: number | null; // –º–∏–Ω—É—Ç—ã, null = –±–µ–∑ –ª–∏–º–∏—Ç–∞
  onExpired?: () => void;
}

export default function Timer({ startedAt, duration, onExpired }: TimerProps) {
  const [timeDisplay, setTimeDisplay] = useState("");
  const [isExpired, setIsExpired] = useState(false);
  const [isWarning, setIsWarning] = useState(false);
  const hasPlayedSound = useRef(false);

  // –ó–≤—É–∫ —É–≤–µ–¥–æ–º–ª–µ–Ω–∏—è
  const playNotificationSound = useCallback(() => {
    if (hasPlayedSound.current) return;
    hasPlayedSound.current = true;

    try {
      const audioContext = new (window.AudioContext ||
        (window as unknown as { webkitAudioContext: typeof AudioContext }).webkitAudioContext)();
      const oscillator = audioContext.createOscillator();
      const gainNode = audioContext.createGain();

      oscillator.connect(gainNode);
      gainNode.connect(audioContext.destination);

      oscillator.frequency.value = 800;
      oscillator.type = "sine";
      gainNode.gain.value = 0.3;

      oscillator.start();
      oscillator.stop(audioContext.currentTime + 0.5);

      // –í—Ç–æ—Ä–æ–π –±–∏–ø
      setTimeout(() => {
        const osc2 = audioContext.createOscillator();
        const gain2 = audioContext.createGain();
        osc2.connect(gain2);
        gain2.connect(audioContext.destination);
        osc2.frequency.value = 1000;
        osc2.type = "sine";
        gain2.gain.value = 0.3;
        osc2.start();
        osc2.stop(audioContext.currentTime + 0.5);
      }, 600);
    } catch {
      // –ë—Ä–∞—É–∑–µ—Ä –Ω–µ –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç Web Audio API
    }
  }, []);

  useEffect(() => {
    const updateTimer = () => {
      const now = new Date();
      const start = new Date(startedAt);
      const elapsedMs = now.getTime() - start.getTime();
      const elapsedMinutes = Math.floor(elapsedMs / 60000);
      const elapsedSeconds = Math.floor((elapsedMs % 60000) / 1000);

      if (duration === null) {
        // –ë–µ–∑ –ª–∏–º–∏—Ç–∞ - –ø–æ–∫–∞–∑—ã–≤–∞–µ–º —Å–∫–æ–ª—å–∫–æ –ø—Ä–æ—à–ª–æ
        const hours = Math.floor(elapsedMinutes / 60);
        const mins = elapsedMinutes % 60;
        setTimeDisplay(
          `${hours.toString().padStart(2, "0")}:${mins.toString().padStart(2, "0")}:${elapsedSeconds.toString().padStart(2, "0")}`
        );
      } else {
        // –° –æ–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–µ–º - –ø–æ–∫–∞–∑—ã–≤–∞–µ–º –æ–±—Ä–∞—Ç–Ω—ã–π –æ—Ç—Å—á—ë—Ç
        const totalSeconds = duration * 60;
        const elapsedTotalSeconds = Math.floor(elapsedMs / 1000);
        const remainingSeconds = totalSeconds - elapsedTotalSeconds;

        if (remainingSeconds <= 0) {
          setTimeDisplay("00:00:00");
          if (!isExpired) {
            setIsExpired(true);
            playNotificationSound();
            onExpired?.();
          }
        } else {
          const hours = Math.floor(remainingSeconds / 3600);
          const mins = Math.floor((remainingSeconds % 3600) / 60);
          const secs = remainingSeconds % 60;
          setTimeDisplay(
            `${hours.toString().padStart(2, "0")}:${mins.toString().padStart(2, "0")}:${secs.toString().padStart(2, "0")}`
          );

          // –ü—Ä–µ–¥—É–ø—Ä–µ–∂–¥–µ–Ω–∏–µ –∑–∞ 5 –º–∏–Ω—É—Ç
          if (remainingSeconds <= 300 && remainingSeconds > 0) {
            setIsWarning(true);
          }
        }
      }
    };

    updateTimer();
    const interval = setInterval(updateTimer, 1000);

    return () => clearInterval(interval);
  }, [startedAt, duration, isExpired, onExpired, playNotificationSound]);

  return (
    <div className="text-center">
      <div
        className={`text-4xl font-mono font-bold ${
          isExpired
            ? "text-red-500 animate-pulse"
            : isWarning
            ? "text-yellow-500"
            : "text-green-400"
        }`}
      >
        {timeDisplay}
      </div>
      <div className="text-xs text-gray-400 mt-1">
        {duration === null ? "–ë–µ–∑ –ª–∏–º–∏—Ç–∞" : isExpired ? "–í—Ä–µ–º—è –∏—Å—Ç–µ–∫–ª–æ!" : "–û—Å—Ç–∞–ª–æ—Å—å"}
      </div>
    </div>
  );
}
